<template>
  <div class="container" ref="toTop">
    <Header :text="info.title"/>
    <div class="cate" v-if="cates && cates.length > 0">
      <ul>
        <li
          v-for="(item, index) in cates"
          :key="item.cId"
          :class="[index === cates.length - 1 ? 'mr' : '', item.cId === activedCate ? 'actived' : '']"
          @click="clickCate(item)"
        >
          {{ item.name }}
        </li>
      </ul>
    </div>
    <product-list ref="pl" :cateId="activedCate"></product-list>
    <!-- 返回顶部 -->
    <top-feedBack :isShow="showToTopFlag" @hiddenFeedBack="hiddenFeedBack" @toTop="fnScrollToTop"></top-feedBack>
  </div>
</template>

<script>
import Header from '@/components/header'
import productList from '../components/productList'
import mixins from '@/components/top-feedBack/mixins.js'
import { getCityCategoryList } from '@/http/service/smartChoice.js'
export default {
  name: 'smartChoiceCity',
  mixins: [mixins],
  components: {
    Header,
    productList,
  },
  data() {
    return {
      info: '',
      loading: false,
      refreshing: false,
      cates: [],
      activedCate: '',
    }
  },
  computed: {},
  methods: {
    clickCate(item) {
      this.activedCate = item.cId
    },
    goBack() {
      this.$keepAliveDestroy()
      this.$store.dispatch('doHistoryBack');
    },
    getCates() {
      getCityCategoryList({
        regionId: this.info.id,
      }).then(res => {
        console.log('this.info:')
        console.log(this.info)
        if (res.success) {
          this.cates = this.$util.strJSONParse(res.data.classify, [])
        } else {
          this.$toast(res.message)
        }
      })
    },
  },
  created() {
    console.log('created')
    const { query } = this.$route
    if (query.id && query.title) {
      const info = { id: query.id, title: query.title }
      console.log(info, '----info----')
      this.info = info
      this.$route.meta.title = this.info.title
      localStorage.setItem('sccRouterInfo', JSON.stringify(info))
    }

    this.getCates()
  },
  activated() {
  },
  // beforeRouteLeave(to, from, next) {
  //   console.log('beforeRouteLeave to ===>')
  //   console.log(to)
  //   if (to.path === '/smartChoiceProvince') {
  //     this.$destroy()
  //   }
  //   next();
  // },
}
</script>

